<template>
  <div>
    <Title />
    <div class="pure-g">
      <div class="pure-u-1 pure-u-md-1-3"></div>
      <div class="pure-u-1 pure-u-md-1-3 bordered-box">
        <div>
          <h2>Register yourself, Sir</h2>
          <div class="pure-form pure-form-aligned">
            <fieldset>
              <div class="pure-control-group">
                <label for="name">Username</label>
                <input type="text" name="name" v-model="name" placeholder="Username" />
              </div>
              <div class="pure-control-group">
                <label for="mail">Email</label>
                <input type="text" name="mail" v-model="mail" placeholder="Email" />
              </div>
              <div class="pure-control-group">
                <label for="password">Password</label>
                <input type="password" name="password" v-model="password" placeholder="Password" />
              </div>
              <div class="pure-control-group">
                <label for="confirm">Confirm password</label>
                <input type="password" name="confirm" v-model="confirm" placeholder="Confirm password" />
              </div>
              <div class="pure-controls">
                <button
                  v-on:click="register"
                  :disabled="sending"
                  class="pure-button pure-button-primary"
                >Register</button> or
                <router-link to="/login">Login</router-link>
              </div>
            </fieldset>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mutations } from "../stores/user";
import Title from "../components/title.vue";
export default {
  components: {
    Title
  },
  data() {
    return {
      name: "",
      mail: "",
      password: "",
      confirm: "",
      sending: false
    };
  },
  methods: {
    register() {
      this.sending = true;
      mutations.register({ name: this.name, mail: this.mail, password: this.password, confirm: this.confirm }).then(
        r => this.$router.push("/"),
        e => this.sending = false
      );
    }
  }
};
</script>

